var MenuItem = React.createClass({
  render () {
    return (
      <DropdownItem onClick={this.loadTooltip}>
        <Link activeClass="fab" to={this.props.navigate} spy={true} smooth={true} duration={1000}>
          <Button variant="fab" color={this.props.buttonColor} data-tip={this.props.tooptip}>
            <FontAwesome  className="mui--align-middle" name={this.props.iconClass} size="2x" />
          </Button>
          <span className="tooltip-text">{this.props.tooltip}</span>
          {/*
          <ReactTooltip id={this.props.tooltip} place="right" type="dark" effect="solid">
            <span>Navigate to {this.props.name}</span>
          </ReactTooltip>
        */}
        </Link>
      </DropdownItem>
    )
  }
});

