class PersonalBox extends React.Component {
  render () {
    var items = this.props.items;
    return (
      <div>
        {items.map(item => 
          <div key={item.id} className="personalBox">
            <div className="personalBox-left">
              <h5>{item.left}</h5>
            </div>
            <div className="personalBox-right">
              <h5>{item.right}</h5>
            </div>
          </div>
          )}
      </div>
    )
  }
}

class Title extends React.Component {
  render () {
    return (
      <h4 className="title">{this.props.name}</h4>
    )
  }
}

class ResumeBox extends React.Component {
  render () {
    var items = this.props.items;
    return (
      <div>
        {items.map(item => 
          <div key={item.id} className="resumeBox">
            <div className="resumeBox-title">
              <h5>{item.time}</h5>
              <span>{item.comment}</span>
            </div>
            <div className="resumeBox-body">
              <h5>{item.title}</h5>
              <p>{item.body}</p>
            </div>
          </div>
          )}
      </div>
    )
  }
}

class ProjectBox extends React.Component {
  render() {
    var items = this.props.items;
    return (
      <div>
        {items.map(item => 
            <div key={item.id} className="resumeBox projectBox">
              <div className="resumeBox-title projectBox-icon hidden-xxs">
                <CircleDiv name={item.icon}/>
              </div> 
              <div className="resumeBox-body projectBox-body">
                <h5>{item.title}</h5>
                <p>{item.body}</p>
              </div>
            </div>
          )}
      </div>
    )
  }
}

class CircleDiv extends React.Component {
  render () {
    return (
      <Button variant="fab" color="default" disabled={true} style={{border: '1px solid lightgrey'}}>
        <FontAwesome className="mui--align-middle" name={this.props.name} size="2x" />
      </Button>
    )
  }
}

class SkillBar extends React.Component {
    render() {
        var options = {
            strokeWidth: 2,
            duration: 1500,
            textAnimation: true
        };
 
        // For demo purposes so the container has some dimensions. 
        // Otherwise progress bar won't be shown 
        var containerStyle = {
            width: '100px',
            height: '100px',
            margin: '0px',
            padding: '0px'
        };
 
        return (
          <div style={{width:'100px'}}>
          <div className="skillBar">
            <Circle
                progress={this.props.progress}
                options={options}
                initialAnimate={true}
                containerStyle={containerStyle}
                containerClassName={'.progressbar'} />
            <p className="skillBar-text">{this.props.progress * 100 + '%'}</p>
            </div>
            <p className="skillBar-name">{this.props.name}</p>
          </div>
        );
    }
};


DropdownMenu.defaultProps = {
  buttonColor: "accent"
}
MenuItem.defaultProps = {
  buttonColor: "accent"
}
IconSlider.defaultProps = {
  sliderColor: "accent"
}
SkillSlider.defaultProps = {
  slides: 3
}



export default App;



